import React, { useEffect, useState } from 'react'
import { supabase } from '../../database/supabaseClient'
import { content } from '../../content/content'
import JsonEditor from '../../componets/JsonEditor/JsonEditor'
import { useStaticConfig } from '../../hooks/useStaticConfig'



export default () => {



const [rows, setRows] = useState([])
const [error, setError] = useState(null)

const { ENABLE_EDITOR } = useStaticConfig();

  
  useEffect(() => {
    async function fetchData() {
      const { data, error } : any = await supabase
        .from('test')
        .select('*')
        .order('id', { ascending: true })

      if (error) {
        console.error("❌ Supabase error:", error)
        setError(error.message)
      } else {
        console.log("✅ Supabase data:", data)
        setRows(data)
      }
    }

    fetchData()
  }, [])

  return (
    <div style={{ padding: 20 }}>
      <h1>Supabase Test Table</h1>
      {error && <p style={{ color: 'red' }}>Error: {error}</p>}
      <ul>
        {rows.map((r : any) => (
          <li key={r.id}>Row ID: {r.id} — Created: {r.created_at}</li>
        ))}
      </ul>

      <JsonEditor  content={content} setContent={()=>{}} toggle={ENABLE_EDITOR}/>
    </div>
  )
}